/* SingleQuestionAnimation.css */

/* --- 1. DEFINE VARIABLES FOR LIGHT MODE (THE DEFAULT) --- */
.photo-container {
  /* Colors & Effects */
  --bg-color: #ffffff;
  --border-color: #cccccc;
  --corner-color: rgba(40, 40, 40, 0.45);
  --box-shadow-style: 5px 5px 15px rgba(0, 0, 0, 0.45); /* Standard drop shadow */

  /* Typography */
  --main-text-color: #333333;
  --underline-color: #000000;
  --hint-text-color: #808080; /* Silver */
  --hint-underline-color: #777777;
}

/* --- 2. OVERRIDE VARIABLES FOR DARK MODE --- */
.photo-container.dark-mode {
  /* Colors & Effects */
  --bg-color: #1e1e1e;
  --border-color: #444444;
  --corner-color: rgba(210, 210, 210, 0.45);
  /* A multi-layered "glow" effect instead of a dark shadow */
  --box-shadow-style: 0 0 8px rgba(255, 255, 255, 0.12),
                      0 0 20px rgba(255, 255, 255, 0.48);

  /* Typography */
  --main-text-color: #eeeeee;
  --underline-color: #dddddd;
  --hint-text-color: #999999;
  --hint-underline-color: #bbbbbb;
}


/* --- 3. APPLY THE VARIABLES TO YOUR ELEMENTS --- */

/* Main Container Styling */
.photo-container {
  position: relative;
  min-height: 220px;
  padding: 10px;
  margin: 1rem; /* Gives the shadow/glow effect space to render */
  cursor: pointer;
  
  /* Apply the theme variables */
  background-color: var(--bg-color);
  border: 1px solid var(--border-color);
  box-shadow: var(--box-shadow-style);
  color: var(--main-text-color);

  /* Add smooth transitions for when the theme changes */
  transition: background-color 0.3s ease, 
              border-color 0.3s ease, 
              box-shadow 0.3s ease,
              color 0.3s ease;
}

/* Base style for the <p> tag holding the answer text */
.photo-container p {
  white-space: pre-wrap;
  margin: 0;
  line-height: 1.8;
}

/* Style for the blank placeholder span that gets measured */
.blank-placeholder {
  display: inline-block;
  min-width: 15px;
  margin: 0 0px;
  padding: 0 0px;
  vertical-align: bottom;
  text-align: left;
  line-height: 1.72;
  font-size: 18px; /* Must match the final animated word font size */
  
  /* Apply the variable for the underline */
  border-bottom: 1px solid var(--underline-color);
}

/* Wrapper for the hint text and underscores */
.hint-wrapper {
  /* Use a monospaced font for clean, even character spacing */
  font-family: 'Courier New', Courier, monospace;
}

/* The visible text part of the hint */
.hint-text {
  color: var(--hint-text-color);
  font-weight: 500;
}

/* The underscore part of the hint */
.hint-underscores {
  letter-spacing: 1.7px; /* Adjust spacing to match your font's underscore width */
  color: var(--hint-underline-color);
}


/* --- 4. PHOTO CORNERS STYLING --- */

/* Base style for all four corner elements */
.corner-element {
  position: absolute;
  width: 20px;
  height: 20px;
  z-index: 1; /* Sits above the container background but below text */
  
  /* Apply the theme variable */
  background-color: var(--corner-color);

  /* Add a smooth transition for the color change */
  transition: background-color 0.3s ease;
}

/* Position and clip each corner into a triangle */
.top-left {
  top: 0;
  left: 0;
  clip-path: polygon(0 0, 100% 0, 0 100%);
}

.top-right {
  top: 0;
  right: 0;
  clip-path: polygon(100% 0, 0 0, 100% 100%);
}

.bottom-left {
  bottom: 0;
  left: 0;
  clip-path: polygon(0 100%, 0 0, 100% 100%);
}

.bottom-right {
  bottom: 0;
  right: 0;
  clip-path: polygon(100% 100%, 100% 0, 0 100%);
}

.photo-container .code-block-container.composed,
.photo-container .code-block-container.composed code,
.photo-container .code-block-container.composed span {
  font-family: inherit !important;
}