.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.filled-word:hover {
  color: yellow;
}

.auto-n_seconds {
  position: fixed;
  color: aqua;
  margin-left: 25%;
  background-color: blue;
  opacity: 0.5;
  z-index: 10;
}
.auto-n_seconds:hover {
  cursor: pointer;
  color: white;
  background-color: darkblue;
  opacity: 0.1;
}

.inline-code {
  background-color: #e0e0e0; /* A light grey background */
  color: #c7254e; /* A reddish color for the text */
  padding: 2px 4px;
  border-radius: 4px;
  font-family: Consolas, "Courier New", monospace;
  font-size: 0.9em;
}

/* For <pre><code> blocks */
.code-block {
  background-color: #2d2d2d; /* A dark background for the block */
  color: #f8f8f2; /* Light text for contrast */
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #444;
  font-family: "Fira Code", Consolas, "Courier New", monospace;
  font-size: 0.9em;
  text-align: left;
  white-space: pre-wrap; /* Allows long lines to wrap */
  overflow-x: auto; /* Adds a scrollbar if a line is still too long */
}

/* Dark mode overrides for code blocks */
body.dark-mode .inline-code {
  background-color: #3e3e3e;
  color: #ff79c6; /* A pinkish color for dark mode */
}

body.dark-mode .code-block {
  /* The default style is already dark, so it looks great! */
  /* You could add more specific dark mode styles here if you wish. */
}

.code-placeholder {
  display: inline-block;
  min-width: 15px;
  border-bottom: 1px solid #888; /* A subtle underline for code */
  vertical-align: bottom;
}
