/* src/CodeHighlighter.css */

/* This is the main container for the highlighted code block */
.code-block-container {
  padding: 1rem;
  border-radius: 8px;
  font-family: 'Fira Code', Consolas, 'Courier New', monospace;
  font-size: 0.9em;
  text-align: left;
  overflow-x: auto;
  
  /* Remove the default <pre> tag margin to prevent extra spacing */
  margin: 1em 0 !important;

  /* THE FIX: Add a subtle border to create visual separation in dark mode */
  border: 1px solid #444; 
}

/* For light mode, use a lighter border */
body:not(.dark-mode) .code-block-container {
  border: 1px solid #ddd;
}

.code-line {
  display: table-row;
}

.line-number {
  display: table-cell;
  text-align: right;
  padding-right: 1.5em;
  opacity: 0.5;
  color: #888; 
}

.line-content {
  display: table-cell;
  width: 100%;
}